import React from "react";
import "./Weather.css";
import history from "../history";

const ChoosePlace = props => {
  return (
    <form
      onSubmit={async e => {
        await props.loadWeather(e);

        history.push("/weather");
      }}
    >
      <input
        className="form-input"
        name="city"
        type="text"
        placeholder="City..."
      />
      <input
        className="form-input"
        name="country"
        type="text"
        placeholder="Country..."
      />
      <button className="submit" type="submit">
        Get weather
      </button>
    </form>
  );
};
export default ChoosePlace;
