import React from "react";
import "./App.css";
import ChoosePlace from "./components/ChoosePlace";
import Weather from "./components/Weather";
import { Router, Switch, Route, Link } from "react-router-dom";
import history from "./history";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      city: "",
      country: "",
      sunrise: 0,
      sunset: 0,
      list: []
    };
  }
  componentDidMount() {
    //guaranteed that this get called when app is render for the first time
    const localColor = localStorage.getItem("theme");

    if (localStorage.getItem("theme") === null) {
      localStorage.setItem("theme", "dark");
      document.body.classList.add("dark");
    } else {
      localStorage.setItem("theme", localColor);
      document.body.classList.add(localColor);
    }
  }

  getWeather = async e => {
    e.preventDefault();
    const city = e.target.elements.city.value;

    const country = e.target.elements.country.value;
    const req = await fetch(
      `http://api.openweathermap.org/data/2.5/forecast?q=${city},${country}&appid=9ec023e8ee32ce8d12a5d6ac6468f69b&units=metric`
    );
    const res = await req.json();

    await this.setState({
      city: res.city.name,
      country: res.city.country,
      sunrise: res.city.sunrise,
      sunset: res.city.sunset,
      list: res.list
    });
  };

  render() {
    console.log(this.state);

    return (
      <div className="App">
        <Router history={history}>
          <div>
            <nav>
              <div className="linksNav">
                <div>
                  <Link to="/"> Home </Link>{" "}
                </div>
                {this.state.city !== "" && (
                  <div>
                    <Link to="/weather"> Weather </Link>
                  </div>
                )}
              </div>
              <div className="theme-toggle" id="switchButton">
                <input
                  id="theme"
                  type="checkbox"
                  onChange={() => {
                    const nextTheme =
                      localStorage.getItem("theme") === "light"
                        ? "dark"
                        : "light";
                    document.body.classList.toggle("light");
                    document.body.classList.toggle("dark");
                    localStorage.setItem("theme", nextTheme);
                  }}
                />
                <label htmlFor="theme"> </label>
                <span className="on">üåô</span>
                <span className="off">‚òÄÔ∏è</span>
              </div>
            </nav>
            <Switch>
              <Route
                exact
                path="/weather"
                component={props => <Weather {...props} weather={this.state} />}
              />
              <Route exact path="/">
                <ChoosePlace loadWeather={this.getWeather} />
              </Route>
            </Switch>
          </div>
        </Router>
      </div>
    );
  }
}

export default App;
