import React from "react";
import "./Weather.css";
import { format, fromUnixTime } from "date-fns";

export default class Weather extends React.Component {
  render() {
    const sunrise = new Date(this.props.weather.sunrise);
    const sunset = new Date(this.props.weather.sunset);
    const currentWeather = this.props.weather.list[0];

    return (
      <main className="main-container">
        <div className="location-and-date">
          <h1 className="location-and-date__location">
            <span>{this.props.weather.city}</span>,{" "}
            <span>{this.props.weather.country}</span>{" "}
          </h1>
          <div>
            {currentWeather &&
              format(new Date(currentWeather.dt_txt), "yyyy-MM-dd")}
          </div>
        </div>

        <div className="current-temperature">
          <div className="current-temperature__icon-container">
            {currentWeather && (
              <img
                className="weatherIcon"
                id="currentWeatherIcon"
                src={require("../icons/" +
                  currentWeather.weather[0].icon +
                  ".svg")}
                alt="icon"
              />
            )}
          </div>
          <div className="current-temperature__content-container">
            <div className="current-temperature__value">
              {Math.round(currentWeather && currentWeather.main.temp)}&deg;
            </div>
            <div className="current-temperature__summary">
              {currentWeather && currentWeather.weather[0].description}
            </div>
          </div>
        </div>

        <div className="current-stats">
          <div>
            <div className="current-stats__value">
              {currentWeather && Math.round(currentWeather.main.temp_max)}&deg;
            </div>
            <div className="current-stats__label">High</div>
            <div className="current-stats__value">
              {currentWeather && Math.round(currentWeather.main.temp_min)}&deg;
            </div>
            <div className="current-stats__label">Low</div>
          </div>
          <div>
            <div className="current-stats__value">
              {currentWeather && Math.round(currentWeather.wind.speed)}mph
            </div>
            <div className="current-stats__label">Wind</div>

            <div className="current-stats__value">
              {" "}
              {currentWeather && currentWeather.rain
                ? currentWeather.rain["3h"].toFixed(1) + " mm"
                : "0 mm"}
            </div>
            <div className="current-stats__label">Rain</div>
          </div>
          <div>
            <div className="current-stats__value">
              {fromUnixTime(sunrise).toLocaleTimeString([], {
                timeStyle: "short"
              })}
            </div>
            <div className="current-stats__label">Sunrise</div>
            <div className="current-stats__value">
              {fromUnixTime(sunset).toLocaleTimeString([], {
                timeStyle: "short"
              })}
            </div>
            <div className="current-stats__label">Sunset</div>
          </div>
        </div>

        <div className="weather-by-hour">
          <h2 className="weather-by-hour__heading"> 1 day's weather</h2>
          <div className="weather-by-hour__container">
            {this.props.weather.list
              .filter((i, index) => index < 8)
              .map((todaysWeather, index) => {
                return (
                  <div
                    key={"todaysWeather" + index}
                    className="weather-by-hour__item"
                  >
                    <div className="weather-by-hour__hour">
                      {fromUnixTime(todaysWeather.dt).toLocaleTimeString([], {
                        timeStyle: "short"
                      })}{" "}
                    </div>

                    <div>
                      <img
                        className="weatherIcon"
                        src={require("../icons/" +
                          todaysWeather.weather[0].icon +
                          ".svg")}
                        alt="icon"
                      />
                    </div>

                    <div>{Math.round(todaysWeather.main.temp)}&deg;</div>
                  </div>
                );
              })}
          </div>
        </div>

        <div className="next-5-days">
          <h2 className="next-5-days__heading">Next 5 days</h2>
          <div className="next-5-days__container">
            {this.props.weather.list.map((weatherByHourItem, index) => {
              return (
                <div key={"weatherByHour" + index} className="next-5-days__row">
                  <div className="next-5-days__date">
                    {format(new Date(weatherByHourItem.dt_txt), "iii")}
                    <div className="next-5-days__label">
                      {format(new Date(weatherByHourItem.dt_txt), "dd/MM")}
                    </div>
                  </div>

                  <div className="next-5-days__low">
                    {Math.round(weatherByHourItem.main.temp_min)}&deg;
                    <div className="next-5-days__label">Low</div>
                  </div>

                  <div className="next-5-days__high">
                    {Math.round(weatherByHourItem.main.temp_max)}&deg;
                    <div className="next-5-days__label">High</div>
                  </div>

                  <div className="next-5-days__icon">
                    <img
                      className="weatherIcon"
                      src={require("../icons/" +
                        weatherByHourItem.weather[0].icon +
                        ".svg")}
                      alt="icon"
                    />
                  </div>

                  <div className="next-5-days__rain">
                    {weatherByHourItem.rain && weatherByHourItem.rain["3h"]
                      ? weatherByHourItem.rain["3h"].toFixed(1) + " mm"
                      : "0 mm"}
                    <div className="next-5-days__label">Rain</div>
                  </div>

                  <div className="next-5-days__wind">
                    {Math.round(weatherByHourItem.wind.speed)}mph
                    <div className="next-5-days__label">Wind</div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </main>
    );
  }
}
